import React from 'react';
import onScan from 'onscan.js';

const keycode = require('keycode');
const debounce = require('lodash/debounce');


function Scanner({ endScanningEventCode, parser, limitSubsequentTime }) {
  // const inputEl = React.useRef(null);
  const [code, setCode] = React.useState('');
  const [error, setError] = React.useState('none');
  const [paste, setPaste] = React.useState('none');
  const returnCode = debounce((finalCode) => {
    parser(finalCode);
  }, limitSubsequentTime, { leading: true, trailing: false });

  // const handleChange = (e) => {
  //   setCode(e.target.value);

  // };

  const handleSubmit = () => {
    returnCode(code);
    setCode('');
  };

  // const handleKeyUp = (e) => {
  //   if (keycode.isEventKey(e, endScanningEventCode)) {
  //     e.stopPropagation();
  //     handleSubmit();
  //   }
  // };

  // const handleBlur = () => {
  //   inputEl.current.focus();
  // };

  // const handleTouch = (e) => {
  //   console.log('touch');
  //   e.preventDefault();
  // };

  // const test = () => {
  //   onScansimulate(document, '1234567890123');
  // };

  React.useEffect(() => {
    // onScan.attachTo(document);
    onScan.attachTo(document, {
      suffixKeyCodes: [9,13], // enter-key expected at the end of a scan
      reactToPaste: true,
      onScan: function(sCode, iQty) { // Alternative to document.addEventListener('scan')
          setCode(sCode);
      },
      onKeyDetect: function(iKeyCode){ // output all potentially relevant key events - great for debugging!
          console.log('Pressed: ' + iKeyCode);
          handleSubmit();
      },
      onScanError: function(oDebug) {
        console.log(oDebug);
        setError(JSON.stringify(oDebug));
      },
      onPaste: function(sPasted, oEvent) {
        console.log(sPasted);
        setPaste(`Pasted: ${JSON.stringify(sPasted)} + Event: ${JSON.stringify(oEvent)} `);
      }
  });

    // inputEl.current.focus();
    // document.addEventListener('touchstart', handleTouch, { passive: false });

    return () => {
      onScan.detachFrom(document);
      // document.removeEventListener('touchstart', handleTouch);
    }
  }, []);

  return (
    <div>
      <p>
        Debug:{error}
      </p>

      <p>
        paste:{paste}
      </p>
      {/* <button onClick={test}>
      test</button> */}
      {/* <input
        className="code-scanner__input"
        ref={inputEl}
        onChange={handleChange}
        onKeyUp={handleKeyUp}
        onBlur={handleBlur}
        value={code}
        type="text"
        /> */}
    </div>
  );
  // inputMode="none"
}
export default Scanner


